@media only screen and (max-width: 768px) {
  .overlay-container {
    align-items: flex-start !important;
  }
  .modal-button {
    width: 94px !important;
  }
}

.overlay-container {
  position: fixed;
  z-index: 10;
  height: 100vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: transparent;
  z-index: 10;
}
.overlay {
  position: absolute;
  top: 0px;
  left: 0px;
  bottom: 0px;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  padding-top: 8rem;
  opacity: 0.85;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 1;
}

.modal-container {
  width: 600px;
  min-width: 240px;
  padding: 2rem;
  box-shadow: 0px 0px 4px #ccc;
  border-radius: 4px;
  background-color: white;
  margin: 1.5rem;
  margin-top: 3rem;
  z-index: 2;
}

.modal-content {
  position: relative;
  padding-left: 2.5rem;
  font-size: 1.2rem;
}

.modal-content.error::after {
  position: absolute;
  top: -4px;
  left: -8px;
  content: "";
  width: 30px;
  height: 30px;
  background-image: url("../../assets/icons/error.png");
  background-size: cover;
}

.modal-content.success::after {
  position: absolute;
  top: -4px;
  left: -8px;
  content: "";
  width: 30px;
  height: 30px;
  background-image: url("../../assets/icons/success.png");
  background-size: cover;
}

.modal-controls {
  text-align: right;
  margin-top: 2rem;
}

.modal-button {
  background-color: transparent;
  border: 2px solid black;
  padding: 0.5rem;
  width: 160px;
  font-size: 1.2rem;
  cursor: pointer;
  transition: background-color 0.3s, color 0.3s;
  margin-left: 1rem;
}

.modal-button:hover {
  background-color: black;
  color: white;
}
