.message-container {
    padding: 2rem 4rem;
}

.info {
    display: flex;
    justify-content: space-between;
    margin: 2rem 0rem;
    flex-wrap: wrap;
}

.subject {
    padding-right: 2rem;
    font-size: 1.1rem;
    font-weight: 600;
}

.email {
    font-size: 1.1rem;
    font-weight: 600;
}

.content { 
    padding: 1rem 1rem 3rem 1rem;
    margin: 2rem 0rem;
    box-shadow: none;
}